.new-design2020 {
  .design-category {
    margin-left: 15px;
    margin-right: 15px;
    max-width: 270px;
  }

  .design-category__img {
    img {
      width: 100%;
      height: 229px;
    }
  }

  .design-category__description {
    padding-top: 20px;
    display: flex;
    flex-direction: column;
  }

  .design-category__description-title {
    font-size: 16px;
    line-height: 19px;
    color: #575757;
    text-transform: uppercase;
    margin-bottom: 4px;

    &:hover {
      color: #575757;
    }
  }

  .design-category__description-quantity {
    font-size: 14px;
    line-height: 17px;
    color: #BEA267;
    margin-bottom: 0;
  }

  @media (max-width: 991.98px) {
    .design-category {
      max-width: 100%;
    }
  }
}