.new-design2020 {
  .visit-us {
    margin-top: 52px;
    margin-bottom: 9px;
  }

  .visit-us__bg {
    background: url("../img/home/image-02.jpg") no-repeat center;
    background-size: cover;
    width: 100%;
    height: 557px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .visit-us__wrapper {
    border-top: 14px solid rgba(255, 255, 255, .8);
    border-right: 15px solid rgba(255, 255, 255, .8);
    border-bottom: 18px solid rgba(255, 255, 255, .8);
    border-left: 15px solid rgba(255, 255, 255, .8);
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 390px;
    width: 390px;
    height: 331px;
    padding: 1px;
    margin-top: 40px;
  }

  .visit-us__wrapper-two {
    background: rgba(255, 255, 255, .8);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 43px 24px 78px;
  }

  .visit-us__title {
    font-size: 28px;
    line-height: 34px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #333333;
    margin-bottom: 29px;
  }

  .visit-us__address {
    font-size: 15px;
    line-height: 18px;
    color: #333333;
    padding-left: 21px;
    padding-right: 20px;
  }
}